<template>
  <div class="product-details">
    <div class="wrapper">
      <div class="images" :class="{ sale: product.sale }">
          <div class="product-img">
            <img :src="getProductImage(product.image_medium)" v-bind:alt="product.name">
          </div>
          <ul class="additional-images">
            <li class="active"><img src="../assets/img/product_1_small_1.jpg"></li>
            <li><img src="../assets/img/product_1_small_2.jpg"></li>
            <li><img src="../assets/img/product_1_small_3.jpg"></li>
            <li><img src="../assets/img/product_1_small_4.jpg"></li>
          </ul>
      </div>
      <div class="details">
        <a href="#" class="back-btn">Back to <span>Women's clothing</span></a>

        <h3>{{ product.name }}</h3>

        <div class="row">
          <span class="product-price">${{ product.price }}</span>
          <div class="product-reviews">
            <span>{{ productRating[2] }} reviews</span>
            <div class="product-reviews--boxes">
                <i v-for="(rating, index) in productRating[1]" :class="{ fill : isHaveRating(index) }"></i>
            </div>
          </div>
        </div>

        <p>
          {{ product.description }}
        </p>

        <div class="product-size">
          <select name="size">
            <option>Select size</option>
            <option>S</option>
            <option>M</option>
            <option>L</option>
          </select>
        </div>

        <div class="product-qty">
          <button class="qty-minus">-</button>
          <input type="text" name="qty" value="1" />
          <button class="qty-plus">+</button>
        </div>
        <button class="add-to-cart" @click="addToCart(product)">Add to cart</button>
      </div>
    </div>

    <RelatedProducts></RelatedProducts>
  </div>
</template>

<script>
  import RelatedProducts from '@/components/RelatedProducts'
  import { globalStore } from '@/store/store.js'

  export default {
    name: 'product-details',
    data () {
      return {
        productId: parseInt(this.$route.params.id),
        products: globalStore.products,
        product: {},
        productRating: []
      }
    },
    components: {
      RelatedProducts
    },
    methods: {
      getProductInfo () {
        this.product = this.products.filter((product) => product.id === parseInt(this.productId))
        this.product = this.product[0]
      },
      getProductImage (image) {
        return require('../assets/img/' + image)
      },
      addToCart (product) {
        globalStore.cart.push(product)
        let cartItems = globalStore.cart

        if (cartItems.length) {
          this.$http.post('https://jsonplaceholder.typicode.com/posts', cartItems)
              .then((response) => {
                console.info('success')
              }, (response) => {
                console.error('error')
              })
        }
      },
      getProductReview () {
        let ratingApiUrl = 'http://stg.api.bazaarvoice.com/data/reviews.json?apiversion=5.4&passkey=caB45h2jBqXFw1OE043qoMBD1gJC8EwFNCjktzgwncXY4&Filter=ProductId:data-gen-moppq9ekthfzbc6qff3bqokie&Sort=Rating:desc&Limit=10'

        this.$http.get(ratingApiUrl).then(response => {
          let rating = response.body.Results.filter((review) => review.Id === this.productId.toString())
          this.productRating.push(rating[0].Rating, rating[0].RatingRange, rating[0].TotalCommentCount)
        }, response => {
          console.error('error')
        })
      },
      getUserGeoLocation () {
        if ('geolocation' in navigator) {
          navigator.geolocation.getCurrentPosition((position) => {
            console.log(position.coords.latitude, position.coords.longitude)
          })
        }
      },
      isHaveRating (index) {
        return index < this.productRating[0]
      }
    },
    beforeMount () {
      this.productId = parseInt(this.$route.params.id)
      this.getProductInfo()
      this.getProductReview()
      this.getUserGeoLocation()
    },
    watch: {
      '$route' (to, from) {
        this.productId = parseInt(this.$route.params.id)
        this.getProductInfo()
      }
    }
  }
</script>

<style lang="scss" scoped>
  .images {
    width: 400px;
    float: left;
    position: relative;

    @media (max-width: 810px) {
      float: none;
      margin: 0 auto;
    }

    @media (max-width: 480px) {
      width: 100%;

      img {
        max-width: 100%;
      }
    }    

    &:after {
      content: "";
      display: table;
      clear: both;
    }

    &.sale {
        &:before {
            content: "";
            width: 50px;
            height: 50px;
            background: url('../assets/sale-banner.jpg') center center no-repeat;
            background-size: contain;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
        }
    }    

    .product-img {
      max-width: 100%;
    }

    ul.additional-images {
      list-style: none;
      padding-left: 0;

      li {
        width: 22.75%;
        display: block;
        float: left;
        margin-right: 3%;

        img {
          max-width: 100%;
        }

        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  .details {
    width: 540px;
    float: left;
    margin-left: 30px;

    @media (max-width: 980px) {
      width: calc(100% - 440px);
    }

    @media (max-width: 810px) {
      width: 400px;
      float: none;
      display: block;
      margin: 30px auto 0;
    }

    @media (max-width: 480px) {
      width: 100%;
    }

    .back-btn {
      color: #8f8f8f;
      font-size: 12px;
      letter-spacing: -0.025em;
      text-decoration: none;
      display: block;
      margin-top: 15px;

      &:before {
        content: "";
        width: 8px;
        height: 12px;
        background: url('../assets/ic-back-arrow.jpg') center center no-repeat;
        background-size: contain;
        display: inline-block;
        margin-right: 9px;
        vertical-align: middle;
        position: relative;
        top: -1px;
      }

      span {
        color: #fd926d;
      }
    }

    h3 {
      font-size: 24px;
      font-weight: 400;
      display: block;
      color: #4a4a4a;
    }

    .row {
      &:after {
        content: "";
        display: table;
        clear: both;
      }
    }

    .product-price {
      font-size: 36px;
      font-weight: bold;
      color: #fd926d;
      float: left;
    }

    .product-reviews {
      margin-top: 14px;
      float: right;

      span {
        font-size: 12px;
        color: #8f8f8f;
        display: block;
        float: left;
        margin-right: 15px;
      }

      .product-reviews--boxes {
        float: left;
        margin-top: 2px;

        i {
          width: 9px;
          height: 9px;
          display: block;
          float: left;
          margin-right: 1px;
          background: #d1d1d1;

          &:last-of-type {
            margin-right: 0;
          }

          &.fill {
            background: #000000;
          }          
        }
      }
    }

    p {
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      color: #8f8f8f;
    }

    .product-size {
      display: block;
      width: 180px;
      height: 40px;
      margin: 25px 0;
      position: relative;

      select {
        font-size: 12px;
        background: #FFF;
        color: #4d4d4d;
        width: 100%;
        height: 100%;
        border: 0;
        outline: 0;
        -webkit-appearance: none; 
        -moz-appearance: none;
        appearance: none;     
        padding-left: 10px;   
      }

      select::-ms-expand { 
          display: none;
      }

      &:after {
        content: "";
        width: 7px;
        height: 4px;
        display: block;
        background-image: url('../assets/ic-arrow-down.jpg');
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        right: 0;
      }      
    }

    .product-qty {
      width: 145px;
      margin-right: 5px;
      display: block;
      float: left;

      &:after {
        content: "";
        display: table;
        clear: both;
      }

      @media (max-width: 370px) {
        float: none;
        clear: both;
      }      

      input {
        width: 47px;
        height: 45px;
        outline: 0;
        border: 0;
        color: #FFF;
        background: #feb298;
        text-align: center;
        font-size: 14px;
        margin-right: 0;
        float: left;
      }

      button {
        width: 47px;
        height: 47px;
        display: block;
        background: #fd926d;
        color: #FFF;
        font-size: 14px;
        text-transform: uppercase;
        outline: 0;
        border: 0;
        cursor: pointer;
        border-bottom: 3px solid #dd6b44;        
        float: left;
      }
    }

    button.add-to-cart {
      float: left;
      font-size: 14px;
      width: 157px;
      height: 47px;
      margin: 0;

      &:after {
        margin-left: 12px;
      }

      @media (max-width: 370px) {
        margin-top: 20px;
        float: none;
        display: block;
      }
    }
  }
</style>



