import Vue from 'vue'
import Router from 'vue-router'
import ProductDetails from '@/components/ProductDetails'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/product/:id',
      name: 'ProductDetails',
      component: ProductDetails
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



