<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    name: 'app'
  }
</script>

<style lang="scss">
  #app {
    font-family: 'Arial', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin-top: 60px;
  }

  body {
    padding: 0;
    margin: 0;
  }

  .wrapper {
    width: 970px;
    display: block;
    margin: 0 auto;

    @media (max-width: 980px) {
      width: 100%;
      padding: 0 20px;
      box-sizing: border-box;
    }

    &:after {
      content: "";
      display: table;
      clear: both;
    }
  }
</style>



