<template>
  <div class="related-products">
    <span class="section-headline">Related Products</span>
    <div class="wrapper">
      <div class="product" v-for="product in relatedProducts" @click="goToProduct(product)"  :class="{ sale: product.sale }">
        <div class="product-img">
            <img :src="getProductImage(product.image_thumbnail)" :alt="product.name">
        </div>
        <div class="product-name">{{ product.name }}</div>
        <div class="product-price">${{ product.price }}</div>
        <button class="add-to-cart">Add to cart</button>
      </div>
    </div>
  </div>
</template>

<script>
  import router from '@/router'
  import { globalStore } from '@/store/store.js'

  export default {
    name: 'product-details',
    data () {
      return {
        productId: parseInt(this.$route.params.id),
        products: globalStore.products,
        relatedProducts: {}
      }
    },
    methods: {
      getRelatedProducts () {
        this.relatedProducts = this.products.filter((product) => product.id !== this.productId)
      },
      getProductImage (image) {
        return require('../assets/img/' + image)
      },
      goToProduct (product) {
        router.push({name: 'ProductDetails', params: { id: product.id }})
      }
    },
    beforeMount () {
      this.getRelatedProducts()
    },
    watch: {
      '$route' (to, from) {
        this.productId = parseInt(this.$route.params.id)
        this.getRelatedProducts()
      }
    }
  }
</script>

<style lang="scss" scoped>
    .related-products {
        margin-bottom: 50px;

        @media (max-width: 980px) {
            text-align: center;
        }
    }

    .section-headline {
        font-size: 15px;
        font-weight: bold;
        text-transform: uppercase;
        display: block;
        margin: 45px 0;
        color: #414141;
        overflow: hidden;
        text-align: center;

        &:before, &:after {
            background-color: #d8d8d8;
            content: "";
            display: inline-block;
            height: 1px;
            position: relative;
            vertical-align: middle;
            width: 50%;            
        }

        &:before {
            right: 8px;
            margin-left: -50%;
        }

        &:after {
            left: 8px;
            margin-right: -50%;
        }
    }

    .product {
        float: left;
        width: calc(25% - 30px);
        margin-right: 30px;
        position: relative;

        &:last-of-type {
            width: calc(25%);
            margin-right: 0;
        }

        @media (max-width: 980px) {
            float: none;
            display: inline-block;
            width: 220px;

            &:nth-of-type(4) {
                display: none;
            }
        }

        @media (max-width: 810px) {
            &:nth-of-type(3) {
                display: none;
            }
        }

        @media (max-width: 560px) {
            margin-bottom: 30px;
        }

        &.sale {
            &:before {
                content: "";
                width: 50px;
                height: 50px;
                background: url('../assets/sale-banner.jpg') center center no-repeat;
                background-size: contain;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

    .product-name {
        display: block;
        font-size: 12px;
        color: #373737;
        text-transform: uppercase;
        text-align: center;
        margin: 8px 0;
    }

    .product-price {
        display: block;
        font-size: 22px;
        color: #373737;
        text-transform: uppercase;
        text-align: center;     
    }
</style>

<style lang="scss">
    button.add-to-cart {
        width: 120px;
        height: 30px;
        display: block;
        margin: 8px auto 0;
        background: #fd926d;
        color: #FFF;
        font-size: 12px;
        text-transform: uppercase;
        outline: 0;
        border: 0;
        cursor: pointer;
        border-bottom: 3px solid #dd6b44;

        &:after {
            content: "";
            display: inline-block;
            width: 11px;
            height: 11px;
            margin-left: 8px;
            position: relative;
            top: - 1px;
            vertical-align: middle;
            background-image: url('../assets/ic-plus-small.png');
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
        }
    }
</style>



